.base {
    border: 0px solid black;
    width:100%;
    height: 100%;
    background-color: #fff;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.header, .footer{
    border: 0px solid #000;
    background-color: #fff;
    margin: 10px;
    text-align: center;
}
.container {
    border: 2px solid black;
    background-color: #fff;
    padding: 20px;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
}
table {
    width: 100%;
    max-width: 800px;
    background-color: white;
    align-content: center;
    margin: 0 auto;    
}
.tableResultado{
    border: 0   px solid red;
    margin-top:5%;
    margin-bottom:5%;
}

.tableResultado td{
    border-bottom: 1px solid #999;
    border-right: 1px solid #999;
    padding: 5px
}
.conteudo{
    padding-top:10%;
    padding-bottom:10%;
}
.campos {
    border: 2px solid red;
    background-color: blue;
    padding: 20px;
    margin: 0 auto;
    width: 100%;
}
input{
    height:30px;
    width:100px;
}
td{
    border: 0px solid #000;
}
.colNivel{
    width: 20px;
}
button{
    height: 40px;
    width: 100%;
}
select{
    height: 40px;
    width: 100%;
}
.botao{
    height: 40px;
    width: 100%;
}
.menu{
    border: 1px solid #000;    
    padding: 5px;
    text-align: center;
    text-decoration: none;
    background-color: rgb(59, 56, 56);
    color:#fff
}
.menu:active{
    border: 1px solid #000;
    text-align: center;
    text-decoration: none;
    background-color: #FDD900;
}
.active{
    border: 1px solid rgb(59, 56, 56);
    padding: 5px;
    text-align: center;
    text-decoration: none;
    background-color: #FDD900;
    color:#fff
}
a{
    color: #000;
}
.botaoMenu td{
    border: 1px solid rgb(59, 56, 56);
    padding-left: 10%;
    border: 1px solid #000;    
    padding: 5px;
    text-align: center;
    text-decoration: none;
    background-color: rgb(59, 56, 56);
    color:#fff;
    padding-left: 80px;
    padding-right: 80px;
}